<template>
  <div class="page">
    <!-- 页头 -->
    <AnimateHeader
      bg-color="#F5F5F5 "
      menu-bg-color="#F0F0F0"
      color="#3A3A3A"
      fill="#3A3A3A"
      stroke="#3A3A3A"
      title="集分宝兑换"
      :on-back-button-click="back"
      :expanded="false"
      :animate="false"
    >
    </AnimateHeader>
    <div class="page-content withdraw-phone">

      <div class="phone-number">
        <div class="title">支付宝账号</div>
        <label>
          <input type="text" :style="{fontSize: accountFontSize + 'px'}" v-model="form.account" placeholder="输入手机或者邮箱">
        </label>
      </div>

      <div class="phone-number">
        <div class="title">兑换集分宝</div>
        <label>
          <input type="tel" style="font-size: 24px;" v-model="form.point" placeholder="输入集分宝个数">
        </label>
      </div>

      <div class="tips-content">
        <span v-if="!warning">可用集分宝 <span class="assertive fontSemibold">{{jfbInfo.balance}}</span> 个</span>
        <span v-if="warning"class="warning">集分宝已超过可兑换个数</span>
        <span class="all-convert" @click="padding()">全部兑换</span>
      </div>

      <button :disabled="buttonDisabled" :class="{'button-active': !buttonDisabled}" class="button button-withdraw" @click="submit()">
        兑换
      </button>
    </div>
  </div>
</template>
<style lang="scss" scoped>
  @import "../../../scss/mixins";

  .withdraw-phone {
    margin-top: 50px;
    background: #FFF;
    padding: 25px 30px 25px 30px;

    .phone-number {
      width: 200px;
      border-bottom: 1px solid #ddd;
      padding-bottom: 3px;
      margin-bottom: 20px;

      .title {
        font-size: 12px;
        color: #B5B5B5;
        line-height: 17px;
        height: 17px;

        @include font-regular();
      }

      label {
        display: block;
        height: 33px;
        width: 100%;

        input {
          transition: font-size 0.1s ease-in;
          height: 33px;
          width: 100%;
          display: inline-block;
          line-height: normal;
          font-size: 24px;
          color: #3A3A3A;
          padding: 0;

          @include font-regular();

          &::-webkit-input-placeholder {
            font-size: 24px;
            color: #DDDDDD;
            line-height: 33px;
            height: 33px;

            @include font-regular();
          }
        }
      }
    }

    .tips-content {
      line-height: 18px;
      font-size: 13px;
      color: #484746;

      @include font-regular();

      .tips {
        margin-top: 5px;
        line-height: 18px;
      }

      .fontSemibold {
        @include font-semibold();
      }

      .all-convert {
        font-weight: 600;
        margin-left: 3px;
        color: #4a90e2;
        text-decoration: underline;
      }

      .warning {
        color: #ef502b;
        font-weight: 600;
        font-size: 13px;

        @include font-semibold();
      }
    }

    .button-withdraw {
      width: 172px;
      height: 40px;
      min-height: 40px;
      background: #DDDDDD;
      border-radius: 2px;
      line-height: 40px;
      margin-top: 30px;
      font-size: 13px;
      color: #FFFFFF;

      @include font-semibold();
    }

    .button.disabled, .button[disabled] {
      opacity: 1;
    }

    .button-active {
      background: #EF4F2A;
    }
  }
</style>
<script>
  import AnimateHeader from '@/widget/animate-header/Index.vue'
  import withdrawOptions from './WithdrawOptions.vue'
  import { popup_wechat_bind, popup_user_freeze, popup_bind_phone } from '../popup.js'
  import {mapState, mapActions, mapGetters} from 'vuex'

  export default {
    components: {
      withdrawOptions,
      AnimateHeader
    },

    data () {
      return {
        form: {
          account: '',
          point: ''
        },
        warning: false
      }
    },

    watch: {
      'form.point': function (val) {
        if (val > this.jfbInfo.balance) {
          this.warning = true
        } else {
          this.warning = false
        }
      }
    },

    computed: {
      ...mapState('withdraw', [
        'info'
      ]),

      ...mapGetters('withdraw', [
        'jfbInfo'
      ]),

      buttonDisabled () {
        if (this.form.account !== '' && this.form.point !== '' && this.form.point <= this.jfbInfo.balance) {
          return false
        }
        return true
      },

      accountFontSize () {
        let len = this.form.account.length
        let fontSize = 24
        if (len >= 16 && len < 24) {
          fontSize = 16
        }
        if (len >= 24 && len < 27) {
          fontSize = 14
        }
        if (len >= 27 && len < 29) {
          fontSize = 13
        }
        if (len >= 29 && len < 32) {
          fontSize = 12
        }
        return fontSize
      }
    },

    mounted () {
      this.fetch()
    },

    methods: {
      fetch () {
        this.getJfbWithdrawList().then(() => {
          this.form.account = this.jfbInfo.account
        })
      },

      padding () {
        this.form.point = this.jfbInfo.balance
      },

      ...mapActions('withdraw', [
        'getJfbWithdrawList',
        'submitJfb'
      ]),

      back () {
        $router.back('/user')
      },

      showBindPhonePopup () {
        let popup = popup = $popup.fromTemplate(
          popup_bind_phone.template,
          popup_bind_phone.options
        )

        popup.show().then(buttonIndex => {
          if (buttonIndex === 1) {
            $router.forward({
              path: '/user/bindMobile/0',
              query: { backroute: '/withdraw/rebatescore' }
            })
          }
        })
      },

      submit () {
        if (this.submitLock) return
        this.submitLock = true
        setTimeout(() => { this.submitLock = false }, 1000)

        if (!/^\d+$/g.test(this.form.point)) {
          $toast.show('请输入正确的集分宝数量')
          return
        }

        if (Number(this.form.point) === 0) {
          $toast.show('集分宝余额不足')
          return
        }

        if (!this.jfbInfo.mobile_bind) {
          this.showBindPhonePopup()
          return
        }

        if (this.jfbInfo.enabled === 0) { // 需要绑定微信
          let p = popup_wechat_bind
          let popup = $popup.fromTemplate(p.template, p.options)
          popup.show().then(btn => {
            if (btn === 0) { // 取消
            } else if (btn === 1) { // 前往微信绑定页面
              $router.forward({
                path: '/user/bindWeixin',
                query: { backroute: '/withdraw/rebatescore', jumproute: '/withdraw/rebatescore' }
              })
            }
          })
          return
        }

        this.submitJfb(this.form).then((res) => {
          if (res.status === 200) {
            if (res.data.err_code === 90000) {
              let popup = $popup.fromTemplate(
                popup_user_freeze.template(),
                popup_user_freeze.options
              )
              popup.show()
            } else {
              $router.forward({
                path: '/withdraw/result/rebatescore'
              })
            }
          }
        })
      }
    }
  }
</script>



