<template>
  <div class="list withdraw-options">
    <div class="option" v-for="(option, index) in options"
         :style="{width: optionWidth + 'px'}"
         :class="{'active': selectedIndex == index}"
         @click="optionSelect(index)">
      <div class="title" v-text="option.title" :class="{'lineHeight30': option.desc == ''}"></div>
      <div class="desc" v-text="option.desc"></div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
  @import "../../../scss/mixins";

  .withdraw-options {
    width: 100%;
    margin: 30px auto;

    .option {
      height: 44px;
      text-align: center;
      color: #3A3A3A;
      font-weight: 600;
      padding: 6px;
      border-radius: 2px;
      border: 1px solid #3A3A3A;
      background: #fff;
      display: inline-block;
      box-sizing: border-box;
      vertical-align: bottom;

      @include mr(10px);
      @include mb(10px);
      @include font-semibold();

      .title {
        font-size: 13px;
        line-height: 18px;
      }

      .lineHeight30 {
        line-height: 30px;
      }

      .desc {
        font-size: 10px;
        line-height: 14px;
      }

      &.active {
        border: 1px solid #EF502B;
        background: #EF502B;
        color: #fff;
      }
    }

    .option:nth-child(even) {
      @include mr(0px);
    }

    .option:nth-last-child(2) {
      @include mb(0px)
    }

    .option:nth-last-child(1) {
      @include mb(0px)
    }

    .option:nth-child(2) {
      @include mb(10px)
    }
  }
</style>
<script>
  export default {
    props: {
      options: Array,
      onSelect: Function,
      selectedIndex: Number
    },
    data () {
      return {
        optionWidth: (document.body.clientWidth - 70) / 2
      }
    },

    methods: {
      optionSelect (index) {
        if (this.onSelect) this.onSelect(index)
      }
    }
  }
</script>



